:root {
    --primary: #8ebd9a;
    --white: white;
}

.btn{
    
    font-family: 'Montserrat';
    padding: 0.3vmax 0.3vmax;
    flex-shrink:2;
    border-radius: 20px;
    width:12vmax;
    cursor: pointer;    
}

.btn:hover {
    -webkit-transform: translate(-3px);
            transform: translate(-3px);
    background-color: #a7c280;
}

.btn--primary--solid {
    background-color: #8ebd9a;
    background-color: var(--primary);
    color:white;
    color:var(--white);
    border: none;
}

.btn--medium {
    padding: 10px 20px;
    font-size: 18px;
}

.btn--small {
    padding: 1vmax 1vmax;
    font-size: 1.0vmax;
}
