    background-size: cover;
  }
  .grid-area-three{
    background-image: url('https://firebasestorage.googleapis.com/v0/b/spotify-moodboard.appspot.com/o/IMG_5766.jpg?alt=media&token=44f19823-4bbe-4def-bad3-1569c8271ba5');
