.App {
  text-align: center;
  background-color: white;
  color:white;
  position: relative;
  font-family: 'Montserrat', 'sans-serif';
}

.collage-wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-rows: 1fr 1fr 1fr 1fr 1fr 1fr;

    width: 100vw;
    height: 100vh;
    
  }
  
  .quad-flex{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1%;   
  }

  .quad-flex>*{
    flex: 1 1 50%;
  }


  div{
    overflow: hidden;
  }
  
  #artist_img{
    flex-shrink: 2;
    max-width: 30%;
    height: 40%;
    padding-left: 10%;
    padding-right: 10%;
    object-fit: cover;
  
  }

  #playlist_img{  
    flex-shrink: 2;
    margin:auto;
    width: 80%;
    max-width: 100%;
    height: auto;
  }

  #album_img{    
    flex-shrink: 2;
    margin:auto;
    width: 20%;
    max-width: 100%;
    height: auto;
  }
  .playlist_label{
    position: relative;
    text-align: center;
  }

  .circular--landscape {
    position: absolute;
    left:80%;
    margin-top: 2%;
    
  }

  .circular--landscape img {
    width: 60%;
    height: 60%;
    border-radius: 50%;
    
    flex-shrink: 2;
    /*margin-top: 12%;*/
    
    flex-grow: 2;
    
  }

  #circle-img { 
    
    border-radius: 50%;
    flex-shrink: 2;
    margin:auto;
    width: 5vmax;
    overflow: hidden;

    object-fit: cover;
    min-height: 60%;
    height: 10vmin;

  
 
  }

  #song_img{
    margin-right:4%;
    flex-shrink: 2;  
    width: 30%;
    max-width: 100%;
    height: auto;
  }
  .margin-btm{
    margin-bottom: 4%;
  }
@media only screen and (max-width:550px){
  .collage-wrapper{
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    overflow: scroll;

  }
}
