/*nine pics total:
  img img img
  img img img
  img img img
*/
#title-margin{
  margin-bottom:-20px;
  
}

#spacing{
  line-height:1.5vmax;
  
}
.cred-flex{
    
    display: flex;
    flex-wrap: wrap;   

  }

  .cred-flex>*{
    flex: 1 1 50%;
  }


  .cred-flex a img{
      height: 12vmax;
      width: 12vmax;
      border-radius: 2vmax;
      padding: 3%;
      object-fit: cover;
  }

  .cred-flex a img:hover{
    transform: scale(1.1);
  }

